button, select {
    text-transform: none;
}

button, input {
    overflow: visible;
}

.seenote-btn {
    
    line-height: 1.5715;
    position: relative;
    display: inline-block;
    font-weight: 400;
    white-space: nowrap;
    text-align: center;
    background-image: none;
    border: 1px solid transparent;
    box-shadow: 0 2px #00000004;
    cursor: pointer;
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    touch-action: manipulation;
    height: 32px;
    padding: 4px 15px;
    font-size: 14px;
    border-radius: 2px;
    color: #666666;
    border-color: #d9d9d9;
    background: #e0e0e0;
}

.seenote-btn, .seenote-btn:active, .seenote-btn:focus {
    outline: 0;
}
.seenote-btn[disabled]{
    color: #00000040;
    border-color: #d9d9d9;
    background: #f5f5f5;
}
.seenote-upload {
    display: inline-block;
}

.seenote-upload input[type=file] {
    cursor: pointer;
} 