.topgun{
    display: flex;
    align-items: center;
    position: fixed;
    top:10px;
    left:50%;
    transform: translateX(-50%);
    padding: 10px 10px;
    background-color: rgb(227, 227, 227);
    border-radius:10px;
    font-size: 12px;
    color: grey;
    z-index: 10;
    button{
        background:none;
        border: none;
        font-size: 20px;
    }
}