*, *:before, *:after {
    box-sizing: border-box;
}

*, body {
    padding: 0;
    margin: 0;

}

.hide {
    display: none !important;
}

.show {
    display: block
}

input {
    border-width: 0 0 1px 0;
}

input:focus-visible {
    outline: none;
}

input[type="file"i] {
    appearance: none;
    background-color: initial;
    align-items: baseline;
    color: inherit;
    text-overflow: ellipsis;
    white-space: pre;
    text-align: start !important;
    padding: initial;
    border: initial;
    overflow: hidden !important;
}

#FileDrop.Hover {
    background-color: black;
    color: white;
}


#FileDrop #Text {
    position: absolute;
    width: 100%;
    height: 20px;
    line-height: 20px;
    left: 0px;
    top: 50%;
    transform: translate(0, -50%);
    text-align: center;
}

textarea {
    font-family: monospace;
    height: 300px;
    width: 100%;
    display: inline-block;
    position: relative;
    /* float: left; */
}

#Results {
    position: relative;
    width: 100%;
    margin: 10px auto;
}

#Description {
    position: relative;
    width: 100%;
    text-align: center;
    height: 40px;
    font-size: 16px;
    margin: 10px auto;
    font-family: sans-serif;
}

tone-play-toggle {
    margin-top: 10px;
}

.demo-panel {
    position: fixed;
