.demo-panel {
    position: fixed;
    right: 0;
    top: 0;
    z-index: 9409;
    min-height: 100%;
    width: 0;
    box-shadow: 0 0 6px rgb(0 0 0 / 25%);
    transition: width .5s;
    font: 14px/24px "Open Sans", Helvetica, Arial, Verdana, sans-serif;
}

.demo-panel.act {
    width: 240px;
}

.switch-panel {
    display: block;
    position: absolute;
    left: -50px;
    top: 150px;
    width: 50px;
    height: 50px;
    box-shadow: 0 0 6px rgb(0 0 0 / 25%);
    background: #fff;
}

.switch-panel a {
    position: relative;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: -ms-flex;
    display: flex;
    height: 50px;
    width: 50px;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    -ms-flex-pack: center;
    -ms-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    -ms-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}

.content-panel {
    /* color: white; */
    width: 100%;
    height: 100%;
    overflow: scroll;
}

.panel-control {
    padding: 20px;
    position: fixed;
    width: 240px;
    right: 0;
    z-index: 1;
    height: 100%;
    background-color: rgb(143 207 247 / 70%);
}

.fileholder {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    white-space: nowrap;
    align-items: center;
}

.recordholder {
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
    align-items: center;
}

.fileholder, .recordholder input {
    width: 100%;
}

.icon-button {
    padding: 0 20px;
    height: 40px;
    border-radius: 10px;
    background: #099b9d;
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
}

.icon-button i {
    width: 20px
}

.icon-button label {
    cursor: inherit;
}

.icon-button span {
    padding: 0 5px
}


:root {

    --green: #0c9d46;
    --sea-green: #099b9d;
    --teal: #049cb7;
    --aqua: #039e8f;
    --sapphire: #3d44c3;
    --cobalt: #5451fb;
    --tile-border-radius: 20px;
    --grid-background-color: #f5f5f7;
    --panel-background-color: #f0f2f5
}

.page-overview {
    margin: 0;
    padding: 0;
    background-color: black;
    position: relative;
    text-align: center;
}

.tile {
    will-change: width, height;
    --tile-background-color: white;
    background-color: #fff;
    border-radius: 20px;
    position: relative;
    display: flex;
    width: 40px;
    margin-left: auto;
    margin-right: auto;
    border-radius: var(--tile-border-radius);
    height: 40px;
    overflow: hidden;
    animation: shrink 1s ease;
    /* transition:
        height .66s cubic-bezier(0.66, 0, 0.01, 1),
        width .66s cubic-bezier(0.66, 0, 0.01, 1),
        color .66s cubic-bezier(0.66, 0, 0.01, 1),
        transform .66s cubic-bezier(0.66, 0, 0.2, 1),
        opacity 100ms linear; */
}

.tile.expanded {
    height: 550px;
    width: 300px;
    z-index: 1;
    animation: expand 1s ease
}

@keyframes expand {
    from {
        width: 40px;
        height:40px;
    }

    to {
        width: 300px;
        height: 550px;
    }
}
@keyframes shrink {
    from {
        width: 300px;
        height:550px;
    }

    to {
        width: 40px;
        height: 40px;
    }
}

.tile-content {
    background-color: var(--panel-background-color);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    padding: 35px 10px 10px;
    opacity: 0;
    /* transition: opacity 100ms linear .66s; */
}

.expanded .tile-content {
    opacity: 1;
    transition: opacity 100ms linear .66s;
}

.panel-left .tile .tile-button-wrapper {
    top: 0px;
    left: 0px;
    position: absolute;
}

.panel-right .tile .tile-button-wrapper {
    top: 0px;
    right: 0px;
    position: absolute;
}

.tile-button-wrapper {
    width: 40px;
    height: 40px;
    bottom: 16px;
    right: 16px;
    cursor: pointer;
}

.tile-button {
    color: var(--sea-green);
    background-color: #fff;
    will-change: transform;
    width: 40px;
    height: 40px;
    opacity: .92;
    z-index: 2;
    transition:
        background-color .66s cubic-bezier(0.66, 0, 0.01, 1) .66s,
        color .66s cubic-bezier(0.66, 0, 0.01, 1) .66s,
        transform .66s cubic-bezier(0.66, 0, 0.2, 1),
        opacity 100ms linear;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.tile .tile-button {
    background-color: white;
    width: 40px;
    height: 40px;
}

.tile-icon-alt {
    fill: currentColor;
    width: 50%;
    height: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.tile.tile-with-overlay {
    --tile-overlay-background-color: var(--teal, #86868b);
    --tile-overlay-copy-max-width: 550px;
}

.tile-overlay {
    z-index: 10;
}

.tile-rounded {
    border-radius: 18px;
}

.tile.tile-with-overlay.expanded .tile-button {
    background-color: var(--panel-background-color);
    transform: rotate(45deg);
    /* transition-delay: 0s; */
    color: var(--tile-overlay-background-color);
}

.panel {
    position: relative;
    width: 100%;
    height: 100%;
}

.panel-left {
    left: 5px;
    top: 5px;
    position: fixed;
}

.panel-right {
    position: fixed;
    right: 5px;
    top: 5px;
}