.dancekey-player{
    width: 80%;
    position: fixed;
    border-radius: 10px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10px;
    display: flex;
    min-width: 500px;
    flex-direction:column;
    button{
        background: none;
        border: none;
        cursor: pointer;
        font-size: 20px;
    }
}
